import React, {useState} from 'react'

export default function Addcomment() {
  const [formData, setFormData] = useState({});

   const handleSubmit = async (event) => {
    event.preventDefault(); // prevent the default form submission behavior

    await fetch('https://majestic-trouble-production.up.railway.app//rodrick/addComment',{
      method:'POST',
      headers:{
        'Accept':'application/json',
        'Content-Type':'application/json'
      },
      body:JSON.stringify(formData),
    })
    .then(response=>{
      console.log(response.json());
    })
    .catch(error=>{
      console.error(error);
    });
    window.location.reload();
  }

  const handleChange = (event) => {
    // update the form data state whenever a form field changes
    setFormData({ ...formData, [event.target.name]: event.target.value });
  }

  return (
    <form className='mt-4' onSubmit={handleSubmit} method='POST'>
      <div className='form-floating mb-3 mt-3'>
      <input
    type="text"
    name="name"
    placeholder='Enter your Fullname'
    onChange={handleChange}
    className="form-control"
    />
    <label htmlFor='name'>Full Name</label>
      </div>

      <div className='form-floating mb-3 mt-3'>
      <input
    type="email"
    name="email"
    placeholder='Enter your Email'
    onChange={handleChange}
    className="form-control"
    />
    <label htmlFor='email'>Enter your Email</label>
      </div>

      <div className='form-floating mb-3 mt-3'>
      <textarea
    type="text"
    name="message"
    placeholder='Add your comment'
    onChange={handleChange}
    className="form-control"></textarea>
    <label htmlFor='message'>Add your comments</label>
      </div>

      <button className='btn btn-lg btn-outline-dark l' type='submit'>SUBMIT</button>

    </form>
  );
}
