import React from 'react'
import Introduction from '../layouts/Introduction'
import Quote from '../layouts/Quote'
import Skills from '../layouts/Skills'
import Numbers from '../layouts/Numbers'
import EducationTimeline from '../layouts/EducationTimeline'
import Form from '../layouts/Form'
import Carousel from '../layouts/Carousel'
import Landingpage from '../layouts/Landingpage'
import Navbar from '../layouts/Navbar'
import Footer from '../layouts/Footer'

export default function Home() {
  const links={
    twitter:"https://twitter.com/RodrickGulu",
    linkedin:"https://www.linkedin.com/in/rodrick-gulu-b37644173/",
    facebook:"https://web.facebook.com/profile.php?id=100083547700490",
    github: "https://github.com/RodrickGulu/RodrickGulu",
  };

  return (
    <div className='mt-3'>
      <Landingpage/>
      <Navbar/>
      <div className='container'>
      <Introduction/>
      <Quote id={Math.floor((Math.random()*4)+1)}/>
      <Skills/>
      <Quote id={Math.floor((Math.random()*4)+5)}/>
      <Numbers/>
      <Quote id={Math.floor((Math.random()*4)+9)}/>
      <Carousel/>
      <Quote id={Math.floor((Math.random()*4)+13)}/>
      <EducationTimeline/>
      <Quote id={Math.floor((Math.random()*4)+17)}/>
      <Form/>
      </div>
      <Footer SocialLinks={links}/>
    </div>
  );
}
