import axios from 'axios';
import React, { useEffect, useState } from 'react'

export default function Numbers() {
  const [data,setSkills]=useState([]);

  useEffect(()=>{
    axios.get("https://majestic-trouble-production.up.railway.app/rodrick/getSkills")
    .then(response=>{
      setSkills(response.data)
    });
  },[]);
  return (
    <section className='site-section mt-5' id='numbers'>
          <div className='container-fluid'>
            <h1>THE NUMBERS</h1>
            <div className='container'>
              <h3>MY SKILLS</h3>
            <div className='progress mt-4'>
              <div className='progress-bar progress-bar-striped progress-bar-animated bg-dark' role="progressbar" aria-valuenow="90" aria-valuemin="0" aria-valuemax="100" style={{"width":"90%"}}>JAVA DEVELOPMENT</div>
            </div>
            <div className='progress mt-4'>
              <div className='progress-bar progress-bar-striped progress-bar-animated bg-dark' role="progressbar" aria-valuenow="75" aria-valuemin="0" aria-valuemax="100" style={{"width":"75%"}}>FRONT-END WITH REACT</div>
            </div>
            <div className='progress mt-4'>
              <div className='progress-bar progress-bar-striped progress-bar-animated bg-dark' role="progressbar" aria-valuenow="80" aria-valuemin="0" aria-valuemax="100" style={{"width":"80%"}}>BACKEND WITH SPRINGBOOT</div>
            </div>
            <div className='progress mt-4'>
              <div className='progress-bar progress-bar-striped progress-bar-animated bg-dark' role="progressbar" aria-valuenow="94" aria-valuemin="0" aria-valuemax="100" style={{"width":"94%"}}>WORKING WITH MYSQL</div>
            </div>
            <div className='progress mt-4'>
              <div className='progress-bar progress-bar-striped progress-bar-animated bg-dark' role="progressbar" aria-valuenow="96" aria-valuemin="0" aria-valuemax="100" style={{"width":"96%"}}>ANY OTHER COMPUTER RELATED TASK</div>
            </div>
            </div>
{data.map(skills=>(

            <div key={skills.id} className='container mt-5'>
              <h3>EXPERIENCE</h3>
              <div className='row mt-2'>
                <div className='col-sm-4'>
                  <h4>CLIENTS I'VE WORKED FOR</h4>
                 <p><i className='fa fa-users fa-5x'></i></p>
                  <p className='fs-1'><span>{skills.clients}</span></p>
                </div>
                <div className='col-sm-4'>
                  <h4>PROJECTS I'VE MADE A BID ON</h4>
                  <p><i className='fa fa-laptop fa-5x'></i></p>
                  <p className='fs-1'>{skills.bids}</p>
                </div>
                <div className='col-sm-4'>
                  <h4>PROJECTS COMPLETED</h4>
                  <p><i className='fa fa-hourglass-end fa-5x'></i></p>
                  <p className='fs-1'><span>{skills.completed}</span></p>
                </div>
                <div className='col-sm-4'>
                  <h4>ONGOING PROJECTS</h4>
                  <p><i className='fa fa-hourglass-half fa-5x'></i></p>
                  <p className='fs-1'><span>{skills.ongoing}</span></p>
                </div>
                <div className='col-sm-4'>
                  <h4>HOURS OF WORK</h4>
                  <p><i className='fa fa-clock-o fa-5x'></i></p>
                  <p className='fs-1'><span>&gt;{skills.hours}</span></p>
                </div>
              </div>
            </div>
            ))}

          </div>
        </section>
  );
}
